@import url(~ag-grid-community/dist/styles/ag-grid.css);
@import url(~ag-grid-community/dist/styles/ag-theme-balham.css);
* {
  padding: 0;
  margin: 0; }

body {
  font-size: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; }

.left {
  float: left; }

.right {
  float: right; }

form * {
  outline: none;
  font-family: inherit;
  font-size: inherit; }

form input[type="text"], form input[type="password"], form input[type="date"] {
  border: 1px solid #dac0c0;
  min-width: 90px;
  border-radius: 14px;
  padding: 10px 5px; }

form select {
  width: 66%;
  max-height: 40px;
  padding: 7px 4px;
  border-radius: 14px; }

form .multi-select {
  width: 66%;
  padding: 7px 4px;
  border-radius: 14px; }

form span.form-error {
  color: #cc0000;
  font-size: 12px;
  display: inline-block;
  margin-top: 5px;
  margin-left: 0px; }
  form span.form-error:before {
    display: inline-block;
    content: '\0021';
    border-radius: 14px;
    padding: 2px 9px;
    background-color: #cc0000;
    color: #FFF;
    margin-right: 3px; }

form .form-row {
  display: flex;
  justify-content: flex-end;
  padding: .5em; }
  form .form-row label {
    padding: .5em 1em .5em 0;
    flex: 1; }
  form .form-row input {
    flex: 2; }
  form .form-row .react-datepicker-wrapper .react-datepicker__input-container {
    width: 256px; }
    form .form-row .react-datepicker-wrapper .react-datepicker__input-container input {
      width: 100%; }
  form .form-row textarea {
    resize: none;
    outline: none;
    /* padding: 20px; */
    flex: 2;
    border-radius: 15px;
    padding: 5px 0 0 7px;
    /* text-align: start; */
    /* align-content: flex-start; */
    vertical-align: text-top; }

button {
  border: none;
  padding: 8px 10px;
  color: #FFF;
  border-radius: 14px;
  cursor: pointer;
  outline: none;
  position: relative; }
  button.app-primary-btn {
    background-color: #3730e1;
    border: 0.4px solid #3730e1; }
    button.app-primary-btn:hover {
      background-color: #3235ac; }
  button:hover {
    top: -4px; }

.data-grid {
  width: 100%;
  min-height: 300px;
  height: 300px;
  max-height: 600px; }

.content-section {
  display: flex;
  align-items: center;
  justify-content: center; }

.user-transaction-details-sec {
  width: 900px;
  margin: 0 auto; }
  .user-transaction-details-sec .add-transaction-sec {
    width: 95.5%;
    display: flex;
    flex-direction: column;
    background: #b1b1b1;
    padding: 20px;
    margin-bottom: 30px; }
    .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec {
      display: flex;
      flex-direction: row; }
      .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li {
        flex: 1;
        list-style: none;
        display: inline-block;
        margin-right: 30px; }
        .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li:nth-child(3), .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li:nth-child(4) {
          margin-top: 30px; }
        .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li label {
          position: relative;
          display: block;
          margin-bottom: 20px;
          cursor: pointer; }
          .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li label input[type="radio"] {
            position: absolute; }
          .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li label.custom-radio-btn-no-label {
            background: #808080;
            z-index: 999999999999;
            text-align: center;
            width: 55.5%;
            padding: 7px 5px; }
            .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li label.custom-radio-btn-no-label:hover {
              box-shadow: -2px 2px 5px #202020; }
            .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li label.custom-radio-btn-no-label input[type="radio"] {
              z-index: 0;
              left: 0;
              top: -1000%; }
            .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li label.custom-radio-btn-no-label:last-child {
              margin-bottom: 0px; }
          .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li label.custom-radio-btn-label {
            padding-left: 20px;
            margin-bottom: 0px; }
            .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li label.custom-radio-btn-label::before {
              margin-left: 5px; }
            .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li label.custom-radio-btn-label input[type="radio"] {
              left: 0;
              top: 6px; }
            .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li label.custom-radio-btn-label:last-child {
              margin-top: 0; }
          .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li label.custom-radio-btn-selected {
            background: #FFF;
            color: #000; }
        .user-transaction-details-sec .add-transaction-sec .add-transaction-form-sec li:last-child {
          margin-right: 0;
          margin-top: 33px; }
  .user-transaction-details-sec .transaction-grid-sec .transaction-graph-sec {
    margin-top: 20px;
    margin-left: -26px; }
